// npm
import React from 'react'
import { FormattedMessage } from 'react-intl'

// self
import Layout from '../components/layout'
import Footer from '../components/footer'
import { withIntl, Link } from '../i18n'

let email = 'robin@millette.info'
let contactForm = false

try {
  const config = require('../../custom/config')
  if (config.email) {
    email = config.email
  }
  if (config.contactForm) {
    contactForm = config.contactForm
  }
} catch (e) {
  console.error('ERRR:', e)
}

const Contact = ({ messages }) => (
  <Layout messages={messages}>
    <div className='container'>
      <h1 className='mt-3'>
        <FormattedMessage id='contact.title' />
      </h1>
      <Content email={email} contactFormEnabled={contactForm} />
    </div>
    <Footer>
      <p className='card-text'>Marvoulous</p>
    </Footer>
  </Layout>
)

const Form = () => (
  <div className='col-sm-12 col-md-9 col-lg-8 col-xl-7'>
    <form>
      <div className='form-group'>
        <label htmlFor='exampleInputEmail1'>Email address</label>
        <input
          type='email'
          className='form-control'
          id='exampleInputEmail1'
          aria-describedby='emailHelp'
        />
        <small id='emailHelp' className='form-text text-muted'>
          We’ll never share your email with anyone else.
        </small>
      </div>
      <div className='form-group'>
        <label htmlFor='exampleInputSubject'>Subject</label>
        <input
          type='text'
          className='form-control'
          id='exampleInputSubject'
        />
      </div>
      <div className='form-group'>
        <label htmlFor='exampleFormControlTextarea1'>Comment</label>
        <textarea
          required
          className='form-control'
          id='exampleFormControlTextarea1'
          aria-describedby='commentHelp'
          rows='3'
        />
        <small id='commentHelp' className='form-text text-muted'>
          Don’t forget to fill in your email address if you expect a response.
        </small>
      </div>
      <button type='submit' className='btn btn-primary btn-block'>
        Submit
      </button>
    </form>
  </div>
)

const Content = ({ email, contactFormEnabled }) => {
  if (contactFormEnabled) {
    return (
      <div className='row'>
        <div className='col order-md-last'>
          <p>
            N’hésitez pas à nous contacter si vous avez des questions ou ou des
            suggestions.
          </p>
          <p>
            Utilisez notre formulaire ou envoyez nous un email:<br />
            <a href={`mailto:${email}`}>{email}</a>
          </p>
          <p>
            Pour contacter un développeur, consultez notre{' '}
            <Link to='/'>répertoire</Link>.
          </p>
        </div>
        <Form />
      </div>
    )
  }

  const pClassName = 'col-sm-12 col-md-4'

  return (
    <div className='row'>
      <div className={pClassName}>
        <p>
          N’hésitez pas à nous contacter si vous avez des questions ou ou des
          suggestions.
        </p>
      </div>
      <div className={pClassName}>
        <p>
          Envoyez nous un email:<br />
          <a href={`mailto:${email}`}>{email}</a>
        </p>
      </div>
      <div className={pClassName}>
        <p>
          Pour contacter un développeur, consultez notre{' '}
          <Link to='/'>répertoire</Link>.
        </p>
      </div>
    </div>
  )
}

export default withIntl(Contact)
